form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 2vmax;
  width: 300px;
  justify-content: space-evenly;
  transition: all 0.5s;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.ReviewFormHeading {
  text-align: center;
  font: 400 2vmax "Roboto";
  text-decoration: underline;
  color: rgba(0, 0, 0, 0.687);
}
form > div {
  display: flex;
  width: 100%;
  align-items: center;
  margin: 2vmax;
}

form > div > input {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.267);
  border-radius: 4px;
  font: 300 0.9vmax cursive;
  outline: none;
}

form > div > svg {
  position: absolute;
  transform: translateX(1vmax);
  font-size: 1.6vmax;
  color: rgba(0, 0, 0, 0.687);
}

.ReviewTable {
  background-color: #fff;
}
.MuiDataGrid-columnHeader {
  background-color: tomato;
  padding: 1vmax !important;
  color: #fff;
}

.ReviewTable button {
  font: 300 1.5vmax "Roboto";
  color: rgba(0, 0, 0, 0.678);
  transition: all 0.5s;
}



.ReviewTable button:hover {
  color: red;
}
.MuiDataGrid-cell {
  font: 300 1.2vmax "Roboto";
}

.noReview{
    text-align: center;
    margin:2rem 0 ;
    font: 300 2.5vmax "Roboto";

}
@media screen and (max-width: 600px) {
  form {
    box-shadow: none;
  }
  .ReviewFormHeading {
    font: 400 4vmax "Roboto";
  }
form > div > input {
    font: 300 2vmax cursive;
    padding: 1vmax 6vmax;
    padding-right: 1vmax;


}
form > div > svg {
font-size: 2.5vmax;
transform: translateX(1.8vmax);


}

  .productListTable a,
  .productListTable button {
    font: 300 3.5vmax "Roboto";
  }

  .MuiDataGrid-cell {
    font: 300 2.2vmax "Roboto";
  }

  .ReviewTable button {
    font: 300 3vmax "Roboto";
    color: rgba(0, 0, 0, 0.678);
    transition: all 0.5s;
  }
}
